import logo from './logo.svg';
import './App.css';

const fetchTestData = function () {
  fetch('/api/v1/tests')
  .then(res => res.json())
  .then((response) => {console.log("test data response", response)})
  .catch((error) => {console.log('error while fetching test data', error)})
}

function App() {
  return (
    <div className="App">
      <button onClick={fetchTestData}>
        Fetch Test Data
      </button>
    </div>
  );
}

export default App;
